import { CST } from "../CST";
export class MapScene extends Phaser.Scene{
    constructor(){
        super({
            key: CST.SCENES.MAP
        })
    }
    init(data){
        console.log('init', data);

        this.nbPieces = data.pieces;
        this.skatePoss = data.skate;
        this.trotPoss = data.trotin;
        this.rollPoss = data.roller;
        this.veloPoss = data.velo;
        this.scootPoss = data.scoot;
    }

    create(){
            console.log(this.nbPieces);
            var piecesCount = this.make.text({
                x: 950,
                y: 50,
                text: this.nbPieces,
                style: {
                    font: '40px Poppins',
                    fill: '#ffffff'
                }
            });
            piecesCount.setDepth(200);
        
    
        
        this.keyboard = this.input.keyboard.addKeys("Q");

        var width = this.game.renderer.width;

        var hautNoir = this.add.graphics();
        hautNoir.fillStyle(0x000000);
        hautNoir.fillRect(0,-30, width+50, 100);
        hautNoir.setScrollFactor(0);
        hautNoir.setDepth(150);
        hautNoir.setRotation(0.02);
 
        var basNoir = this.add.graphics();
        basNoir.fillStyle(0x000000);
        basNoir.fillRect(0,420, width+50, 100);
        basNoir.setScrollFactor(0);
        basNoir.setDepth(150);
        basNoir.setRotation(0.02);

        var fondNoir = this.add.graphics();
        fondNoir.fillStyle(0x000000);
        fondNoir.setDepth(160);
        fondNoir.fillRect(1000,0,1000,500);




        this.time.addEvent({
            delay: 750,
            callback: ()=>{

        this.tweens.add({
            targets: hautNoir,
            x: 1200,
            y: 30,
            ease: 'Sine.easeInOut',
            duration: 500,
            yoyo: false,
            repeat: 0,
        });

        this.tweens.add({
            targets: basNoir,
            x: -1200,
            y: -30,
            ease: 'Sine.easeInOut',
            duration: 500,
            yoyo: false,
            repeat: 0,
        });
        btnForet1.setInteractive();
        btnMntg1.setInteractive();
        btnVille1.setInteractive();
        btnShop1.setInteractive();

            },
            loop: false

        })

        let fond = this.add.image(500,250, "mapFond").setScale(0.12);

        let btnForet1 = this.add.image(50,375, "nivForetBase").setScale(0.12);
        let btnForet2 = this.add.image(50,375, "nivForetActive").setScale(0.12);

        let btnMntg1 = this.add.image(500,250, "nivMontagneBase").setScale(0.12);
        let btnMntg2 = this.add.image(500,250, "nivMontagneActive").setScale(0.12);

        let btnVille1 = this.add.image(950,325, "nivVilleBase").setScale(0.12);
        let btnVille2 = this.add.image(950,325, "nivVilleActive").setScale(0.12);

        let btnShop1 = this.add.image(500,450, "nivMontagneBase").setScale(0.12);
        let btnShop2 = this.add.image(500,450, "nivMontagneActive").setScale(0.12);

        let shopIcon = this.add.image(500,400, "shopIcon").setScale(0.12);


        // Bouton Forêt interactivité
        let nomNiv1 = this.add.image(150, 300, "niv1").setScale(0.5);
        nomNiv1.alpha = 0;

        btnForet1.on("pointerover", ()=>{
            this.tweens.add({
                targets: btnForet1,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnForet1.on("pointerover", ()=>{
            this.tweens.add({
                targets: nomNiv1,
                alpha: 1,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnForet1.on("pointerout", ()=>{
            this.tweens.add({
                targets: btnForet1,
                scaleX: 0.12,
                scaleY: 0.12,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        btnForet1.on("pointerout", ()=>{
            this.tweens.add({
                targets: nomNiv1,
                alpha: 0,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnForet1.on("pointerup", ()=>{
            this.tweens.add({
                targets: hautNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
    
            this.tweens.add({
                targets: basNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
            this.time.addEvent({
                delay: 500,
                callback: ()=>{

                    this.tweens.add({
                        targets: fondNoir,
                        x: -1000,
                        y: 0,
                        ease: 'Sine.easeInOut',
                        duration: 500,
                        yoyo: false,
                        repeat: 0,
                    });

                },
                loop: false

            })

            this.time.addEvent({
                delay: 1500,
                callback: ()=>{
    
            console.log('pointerover');
            this.scene.start(CST.SCENES.CHOIX, {niv: 1, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
            this.sound.stopAll();
                },
                loop: false

            })
        })



        

        // Bouton Montagne interactivité
        let nomNiv2 = this.add.image(500, 175, "niv2").setScale(0.5);
        nomNiv2.alpha = 0;


        btnMntg1.on("pointerover", ()=>{
            this.tweens.add({
                targets: btnMntg1,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnMntg1.on("pointerover", ()=>{
            this.tweens.add({
                targets: nomNiv2,
                alpha: 1,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })

        btnMntg1.on("pointerout", ()=>{
            
            this.tweens.add({
                targets: btnMntg1,
                scaleX: 0.12,
                scaleY: 0.12,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        btnMntg1.on("pointerout", ()=>{
            this.tweens.add({
                targets: nomNiv2,
                alpha: 0,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnMntg1.on("pointerup", ()=>{
           

            this.tweens.add({
                targets: hautNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
    
            this.tweens.add({
                targets: basNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });this.time.addEvent({
                delay: 500,
                callback: ()=>{

                    this.tweens.add({
                        targets: fondNoir,
                        x: -1000,
                        y: 0,
                        ease: 'Sine.easeInOut',
                        duration: 500,
                        yoyo: false,
                        repeat: 0,
                    });

                },
                loop: false

            })
            this.time.addEvent({
                delay: 1500,
                callback: ()=>{
    
            console.log('pointerover');
            this.scene.start(CST.SCENES.CHOIX, {niv: 2, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
            this.sound.stopAll();
                },
                loop: false

            })
        })


        // Bouton Ville interactivité

        
        btnVille1.on("pointerover", ()=>{
            this.tweens.add({
                targets: btnVille1,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnVille1.on("pointerout", ()=>{
            this.tweens.add({
                targets: btnVille1,
                scaleX: 0.12,
                scaleY: 0.12,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        btnVille1.on("pointerup", ()=>{
            this.tweens.add({
                targets: hautNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
    
            this.tweens.add({
                targets: basNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
            this.time.addEvent({
                delay: 500,
                callback: ()=>{

                    this.tweens.add({
                        targets: fondNoir,
                        x: -1000,
                        y: 0,
                        ease: 'Sine.easeInOut',
                        duration: 500,
                        yoyo: false,
                        repeat: 0,
                    });

                },
                loop: false

            })
            this.time.addEvent({
                delay: 1000,
                callback: ()=>{
    
            console.log('pointerover');
            this.scene.start(CST.SCENES.CHOIX, {niv: 3, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
            this.sound.stopAll();
                },
                loop: false

            })
        })






        // Bouton SHOP interactivité


        btnShop1.on("pointerover", ()=>{
            this.tweens.add({
                targets: btnShop1,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnShop1.on("pointerover", ()=>{
            this.tweens.add({
                targets: shopIcon,
                scaleX: 0.15,
                scaleY: 0.15,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })

        btnShop1.on("pointerout", ()=>{
            
            this.tweens.add({
                targets: btnShop1,
                scaleX: 0.12,
                scaleY: 0.12,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        btnShop1.on("pointerout", ()=>{
            this.tweens.add({
                targets: shopIcon,
                scaleX: 0.12,
                scaleY: 0.12,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            }) 
        })
        btnShop1.on("pointerup", ()=>{
           

            this.tweens.add({
                targets: hautNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });
    
            this.tweens.add({
                targets: basNoir,
                x: 0,
                y: 0,
                ease: 'Sine.easeInOut',
                duration: 500,
                yoyo: false,
                repeat: 0,
            });this.time.addEvent({
                delay: 500,
                callback: ()=>{

                    this.tweens.add({
                        targets: fondNoir,
                        x: -1000,
                        y: 0,
                        ease: 'Sine.easeInOut',
                        duration: 500,
                        yoyo: false,
                        repeat: 0,
                    });

                },
                loop: false

            })
            this.time.addEvent({
                delay: 1500,
                callback: ()=>{
    
            console.log('pointerover');
            this.scene.start(CST.SCENES.SHOP, {pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
            this.sound.stopAll();
                },
                loop: false

            })
        })






    }

    update(){
        if(this.keyboard.Q.isDown === true){
            this.scene.start(CST.SCENES.MENU, {pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        }
    }



}