// on importe les différents fichiers javascript associés

import { CST } from "../CST";


// on exporte la classe en cours pour qu'elle soit utilisée dans les autres classes

export class LoadScene extends Phaser.Scene{

    // le constructeur de la classe. Dans phaser, c'est un système de scène : on charge la scène associées qui provient
    // de CST.js

    constructor(){
        super({
            key: CST.SCENES.LOAD
        })
    }

    /* dans phaser 3, il y a plusieurs fonctions essentielles au bon fonctionnement d'une scène : 
    init() : cette fonction sert à initialiser la scène.
    preload() : cette fonction sert à charger les documents nécessaire pour la scène actuelle.
    create() : cette fonction instancie les documents chargés dans la preload
    update() : cette fonction met à jour les différents élements créés dans la fonction create, par
               exemple au niveau du déplacement d'un personnage. 
    */

    init(){}

    preload(){
        











        
        //load image, spritesheet, sound

        // background TITLE ; toutes les images utilisées dans le jeu

        this.load.image("back", "./assets/fondTitle/back.png");
        this.load.image("montagne1", "./assets/fondTitle/montagne1.png");
        this.load.image("montagne2", "./assets/fondTitle/montagne2.png");
        this.load.image("nuage1", "./assets/fondTitle/nuage1.png");
        this.load.image("nuage2", "./assets/fondTitle/nuage2.png");
        this.load.image("plan1", "./assets/fondTitle/plan1.png");
        this.load.image("plan2", "./assets/fondTitle/plan2.png");
        this.load.image("soleil", "./assets/fondTitle/soleil.png");

        // Shop ; les images de la scène magasin

        this.load.image("frontShop", "./assets/Shop/FRONT.png");
        this.load.image("backShop", "./assets/Shop/BACK.png");
        this.load.image("rond", "./assets/Shop/rond.png");
        this.load.image("ficheSkate", "./assets/Shop/ficheSkate.png");
        this.load.image("ficheRoller", "./assets/Shop/ficheRoller.png");
        this.load.image("ficheTrot", "./assets/Shop/ficheTrot.png");
        this.load.image("ficheVelo", "./assets/Shop/ficheVelo.png");
        this.load.image("ficheScoot", "./assets/Shop/ficheScooter.png");


        // Writing TITLE ; écritures utilisées pour le jeu

        this.load.image("options", "./assets/options.png");
        this.load.image("continue", "./assets/continue.png");
        this.load.image("newGame", "./assets/NewGame.png");
        this.load.image("credits", "./assets/credits.png");
        this.load.image("logo", "./assets/logo.png");

        // icones Alternatives

        this.load.image("veloIcon", "./assets/logoAlt/veloIcon.png");
        this.load.image("skateIcon", "./assets/logoAlt/skateIcon.png");
        this.load.image("piedIcon", "./assets/logoAlt/piedIcon.png");
        this.load.image("rollIcon", "./assets/logoAlt/rollIcon.png");
        this.load.image("trotIcon", "./assets/logoAlt/trotIcon.png");
        this.load.image("scootIcon", "./assets/logoAlt/scootIcon.png");


        this.load.image("veloIconNon", "./assets/logoSupr/veloNon.png");
        this.load.image("skateIconNon", "./assets/logoSupr/skateNon.png");
        this.load.image("rollIconNon", "./assets/logoSupr/rollerNon.png");
        this.load.image("trotIconNon", "./assets/logoSupr/trotNon.png");
        this.load.image("scootIconNon", "./assets/logoSupr/scooterNon.png");

        // frames ; les framesheets pour les animations

        this.load.spritesheet("cat", "./assets/cat.png", {
            frameHeight: 32,
            frameWidth: 32
        }); 

        this.load.spritesheet("coeurs", "./assets/coeurs.png", {
            frameHeight: 157,
            frameWidth: 330
        });

        // sounds ; les musiques utilisées dans le jeu 

        this.load.audio("overcalculation", "./assets/Overcalculation.mp3");

        this.load.audio("boutonBruit", "./assets/boutonBruit.mp3");

        this.load.audio("coinSound", "./assets/coin.mp3");


        // plateforms ; les images de fond et les plateformes

        this.load.image("iut", "./assets/iut.jpg");



        // Niveau1 ; les assets pour le premier niveau

        this.load.spritesheet("girl1", "./assets/niv1/frames1.png", {
            frameHeight: 749,
            frameWidth: 280
        });

        this.load.spritesheet("skate1", "./assets/niv1/skate1.png", {
            frameHeight: 749,
            frameWidth: 453
        });

        this.load.image("niv1", "./assets/niv1/niv1.png");

        this.load.image("fond1", "./assets/niv1/background1.png");

        this.load.image("platform11", "./assets/niv1/plat11.png");

        this.load.image("platform12", "./assets/niv1/plat12.png");

        this.load.image("bad1", "./assets/niv1/roc11.png");

        this.load.image("arretBus1", "./assets/niv1/bus1.png");





        // Niveau2 ; les assets pour le deuxième niveau

        this.load.spritesheet("girl", "./assets/niv2/frames.png", {
            frameHeight: 749,
            frameWidth: 280
        });

        this.load.spritesheet("skate", "./assets/niv2/skate.png", {
            frameHeight: 749,
            frameWidth: 453
        });

        this.load.image("niv2", "./assets/niv2/niv2.png");

        this.load.image("fond", "./assets/niv2/background.png");

        this.load.image("platform", "./assets/niv2/plat1.png");

        this.load.image("platform2", "./assets/niv2/plat2.png");

        this.load.image("bad", "./assets/niv2/roc1.png");

        this.load.image("arretBus", "./assets/niv2/bus.png");

        this.load.image("coin","./assets/bad.png");


        // ecranMap ; les images liées à la scene de la carte

        
        this.load.image("mapFond", "./assets/ecranMap/fond.jpg");

        this.load.image("shopIcon", "./assets/ecranMap/shopIcon.png");

        this.load.image("nivForetBase", "./assets/ecranMap/nivForet.png");
        this.load.image("nivForetActive", "./assets/ecranMap/nivForetActive.png");

        this.load.image("nivMontagneBase", "./assets/ecranMap/nivMontagne.png");
        this.load.image("nivMontagneActive", "./assets/ecranMap/nivMontagneActive.png");

        this.load.image("nivVilleBase", "./assets/ecranMap/nivVille.png");
        this.load.image("nivVilleActive", "./assets/ecranMap/nivVilleActive.png");
        
        







        
        
        //Création d'une barre de chargement
        
        //pour simplifier les valeurs : des variables height et width correspondant à la taille de l'écran

        var height = this.game.renderer.height;
        var width = this.game.renderer.width;

        
        
        //instanciation d'une variable loadingBar que l'on complète ultélieurement
        
        var loadingBar = this.add.graphics();
        loadingBar.fillStyle(0xffffff, 1);
        loadingBar.setDepth(2);

        //création d'un commentaire, un texte que l'on peut placer dans la scene
        
        var comment = this.make.text({
            x: width / 2,
            y: height / 2 + 50,
            text: 'Projet Tuteuré - Groupe 2',
            style: {
                font: '18px Arcon',
                fill: '#ffffff'
            }
        });
        comment.setOrigin(0.5, 0.5);
        
        //creation d'un rectangle simple contenant la barre de chargement

        var loadingBox = this.add.graphics();
        loadingBox.fillStyle(0x555555, 0.3);
        loadingBox.setDepth(0);
        loadingBox.fillRect(width * 0.250 - 25, height * 0.73, width * 0.5 +50, 60, 10);
        
        //creation d'un texte correspondant au pourcentage de la barre de chargement

        var percentText = this.make.text({
            x: width / 2,
            y: height / 1.27,
            text: '0%',
            style: {
                font: '18px Broadway',
                fill: '#000000'
            }
        });
        percentText.setOrigin(0.2, 0.5);
        percentText.setDepth(3);
        
       

        /*
        Loader Events
            complete - when done loading everything
            progress - loader number progress in decimal
        */

        //simulate large load

        /*

        for(let i = 0; i<100; i++ ){
            this.load.spritesheet("cat" + i, "./assets/car.png", {
                frameHeight: 32,
                frameWidth: 32
            });         }
        }
        
        */

        this.load.on("progress", (percent)=>{
            loadingBar.fillRect(this.game.renderer.width / 2, this.game.renderer.height * 1.5, this.game.renderer.width * percent, 75).setScale(0.5);
            console.log(percent);
            percentText.setText(parseInt(percent * 100) + '%');
        })

        this.load.on("complete", ()=>{
            //this.scene.start(CST.SCENES.MENU, "hello from LoadScene");
        }) 



    }
    create(){
        this.scene.start(CST.SCENES.MENU);
    }   
}