import { CST } from "../CST";
export class MenuScene extends Phaser.Scene{
    constructor(){
        super({
            key: CST.SCENES.MENU
        })
    }
    init(data){
        console.log(data)

        this.nbPieces = data.pieces;
        this.skatePoss = data.skate;
        this.trotPoss = data.trotin;
        this.rollPoss = data.roller;
        this.veloPoss = data.velo;
        this.scootPoss = data.scoot;

    }

    create(){ //creating the menu screen
        
        //create images (z order)

        let s;

        let logo = this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.4, "logo").setScale(0.2).setDepth(58);
 

        let back = this.add.image(-20,-50, "back").setOrigin(0).setDepth(0).setScale(0.55);
        let soleil = this.add.image(-20,-50, "soleil").setOrigin(0).setDepth(3).setScale(0.55);
        let nuage1 = this.add.image(-20,-50, "nuage1").setOrigin(0).setDepth(4).setScale(0.55);
        let nuage2 = this.add.image(-20,-50, "nuage2").setOrigin(0).setDepth(5).setScale(0.55);
        let montagne2 = this.add.image(-20,-50, "montagne2").setOrigin(0).setDepth(6).setScale(0.55);
        let montagne1 = this.add.image(-20,-50, "montagne1").setOrigin(0).setDepth(7).setScale(0.55);
        let plan2 = this.add.image(-20,-50, "plan2").setOrigin(0).setDepth(8).setScale(0.55);
        let plan1 = this.add.image(-20,-50, "plan1").setOrigin(0).setDepth(9).setScale(0.55);



        let newButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.65, "newGame").setScale(0.2).setDepth(45);
    
        let continueButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.70, "continue").setScale(0.2).setDepth(33);

        let optionButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.75, "options").setScale(0.2).setDepth(53);
    
        let creditButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.80, "credits").setScale(0.2).setDepth(53);
    

        //create sprites (if pixel art, remove sharpen)

        let hoverSprite = this.add.sprite(100, 100, "girl").setDepth(43);
        hoverSprite.setScale(0.1);
        hoverSprite.setVisible(false);

        //create audio, disable pauseonblur

        this.sound.pauseOnBlur = false;
        this.sound.play("overcalculation", {
            loop: true
        });
        this.sound.volume = 0.2
 
        //create animation

        this.anims.create({
            key: "walk",
            frameRate: 40,
            repeats: -1, //repeat forever
            frames: this.anims.generateFrameNumbers("girl", {
                frames: [0,1,2,3]
            })
        })


        //make image buttons interactive

        /*
            PointerEvents
                pointerover - hovering
                pointerout - not hovering
                pointerup - click and release
                pointerdown - just click
        */
        

        // petits traits

        var height = this.game.renderer.height;
        var width = this.game.renderer.width;


        // newButton interactif
        newButton.setInteractive();

        newButton.on("pointerover", ()=>{



            this.tweens.add({
                targets: newButton,
                scaleX: 0.3,
                scaleY: 0.3,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })

        newButton.on("pointerout", ()=>{


            this.tweens.add({
                targets: newButton,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })

        newButton.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(CST.SCENES.CHOIX, {niv: 1, pieces: 0, skate: 0, roller: 0, trotin: 0, velo: 0, scoot: 0});
            this.sound.stopAll();
        })

        // continue interactif
        continueButton.setInteractive();

        continueButton.on("pointerover", ()=>{
            this.tweens.add({
                targets: continueButton,
                scaleX: 0.3,
                scaleY: 0.3,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })

        continueButton.on("pointerout", ()=>{
            this.tweens.add({
                targets: continueButton,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })

        continueButton.on("pointerup", ()=>{
            this.scene.start(CST.SCENES.MAP, {pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
            this.sound.stopAll();
        })

        // options interactif
        

        optionButton.setInteractive();

        optionButton.on("pointerover", ()=>{
            this.tweens.add({
                targets: optionButton,
                scaleX: 0.3,
                scaleY: 0.3,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
           
        })

        optionButton.on("pointerout", ()=>{
            this.tweens.add({
                targets: optionButton,
                scaleX: 0.2,
                scaleY: 0.2,
                ease: 'easeOut',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })

        optionButton.on("pointerdown", ()=>{
            
        })

         // options interactif
        

         creditButton.setInteractive();

         creditButton.on("pointerover", ()=>{
             this.tweens.add({
                 targets: creditButton,
                 scaleX: 0.3,
                 scaleY: 0.3,
                 ease: 'easeIn',
                 duration: 125,
                 yoyo: false,
                 repeat: 0
             })
            
         })
 
         creditButton.on("pointerout", ()=>{
             this.tweens.add({
                 targets: creditButton,
                 scaleX: 0.2,
                 scaleY: 0.2,
                 ease: 'easeOut',
                 duration: 125,
                 yoyo: false,
                 repeat: 0
             })
         })
 
         creditButton.on("pointerdown", ()=>{
            this.scene.start(CST.SCENES.CREDIT);
            this.sound.stopAll();
         })

        logo.alpha = 0;

        this.tweens.add({
            targets: logo,
            y: 175,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });
        this.tweens.add({
            targets: logo,
            alpha: 1,
            ease: 'Sine.easeInOut',
            duration: 1500,
            yoyo: false,
            repeat: 0,
        });




        //animation fond

        this.tweens.add({
            targets: soleil,
            y: -60,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.tweens.add({
            targets: montagne2,
            y: -60,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.tweens.add({
            targets: montagne1,
            y: -65,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.tweens.add({
            targets: nuage1,
            x: -60,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.tweens.add({
            targets: nuage2,
            x: 30,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.tweens.add({
            targets: plan2,
            y: -75,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1, 
        });

        this.tweens.add({
            targets: plan1,
            y: -85,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });


    }
    
    
    }



    


    function update(){}