/** @type {import("../typing/phaser")} */

import {LoadScene} from "./scenes/LoadScene";
import {MenuScene} from "./scenes/MenuScene";
import {Niveau2Scene} from "./scenes/Niveau2Scene";
import {Niveau1Scene} from "./scenes/Niveau1Scene";
import {CreditScene} from "./scenes/CreditScene";
import {ChoixAlternativeScene} from "./scenes/ChoixAlternativeScene";
import {MapScene} from "./scenes/MapScene";
import {ShopScene} from "./scenes/ShopScene";

// creation d'une instance de jeu via PHASER 3.16
let game = new Phaser.Game({
    type: Phaser.AUTO,
    //taille de l'écran de jeu
    width: 1000,
    height: 500,
    //différentes scènes (ecrans) comme le menu, la carte du monde, le chargement... 
    scene:[
       LoadScene, MenuScene, Niveau2Scene, Niveau1Scene, CreditScene, ChoixAlternativeScene, MapScene, ShopScene
    ],
    //creation de la physique du jeu. Ici en arcade, avec un gravité prédéfinie pour coller au style platformer
    physics: {
        default: "arcade",
        arcade: {
            gravity: {y: 800},
            debug: true
        }
    },
    
});


