import { CST } from "../CST";
export class ChoixAlternativeScene extends Phaser.Scene {
    constructor(){
        super({
            key: CST.SCENES.CHOIX
        });
        
    }
    
    init(data){
        console.log('init', data);

        this.nivID = data.niv;
        this.nbPieces = data.pieces;
        this.skatePoss = data.skate;
        this.trotPoss = data.trotin;
        this.rollPoss = data.roller;
        this.veloPoss = data.velo;
        this.scootPoss = data.scoot;
    }
  
    create(){
        let niveau;
        if(this.nivID === 1){
            niveau = CST.SCENES.PLAY1;
        } else if(this.nivID === 2){
            niveau = CST.SCENES.PLAY2;
        } else if(this.nivID === 3){
            niveau = CST.SCENES.PLAY3;
        }


        var fondNoir = this.add.graphics();
        fondNoir.fillStyle(0x000000);
        fondNoir.setDepth(160);
        fondNoir.fillRect(0,0,1000,500);
                this.tweens.add({
                    targets: fondNoir,
                    x: -1000,
                    y: 0,
                    ease: 'Sine.easeInOut',
                    duration: 500,
                    yoyo: false,
                    repeat: 0,
                });




                let srcSkateIcon = "skateIconNon";
                let srcRollIcon = "rollIconNon";
                let srcTrotIcon = "trotIconNon";
                let srcVeloIcon = "veloIconNon";
                let srcScootIcon = "scootIconNon";

                if(this.skatePoss === 1){
                    srcSkateIcon = "skateIcon";
                }
                if(this.rollPoss === 1){
                    srcRollIcon = "rollIcon";
                }
                if(this.trotPoss === 1){
                    srcTrotIcon = "trotIcon";
                }
                if(this.veloPoss === 1){
                    srcVeloIcon = "veloIcon";
                }
                if(this.scootPoss === 1){
                    srcScootIcon = "scootIcon";
                }


        

        let piedIcon = this.add.image(50, this.game.renderer.height/2 -50, "piedIcon").setOrigin(0).setDepth(3).setScale(0.02);
        let skateIcon = this.add.image(200, this.game.renderer.height/2 -50, srcSkateIcon).setOrigin(0).setDepth(3).setScale(0.02);
        let rollIcon = this.add.image(350, this.game.renderer.height/2 -50, srcRollIcon).setOrigin(0).setDepth(3).setScale(0.02);
        let trotIcon = this.add.image(this.game.renderer.width -450, this.game.renderer.height/2 -50, srcTrotIcon).setOrigin(0).setDepth(3).setScale(0.02);
        let veloIcon = this.add.image(this.game.renderer.width -300, this.game.renderer.height/2 -50, srcVeloIcon).setOrigin(0).setDepth(3).setScale(0.02);
        let scootIcon = this.add.image(this.game.renderer.width -150, this.game.renderer.height/2 -50, srcScootIcon).setOrigin(0).setDepth(3).setScale(0.02);

        if(this.skatePoss === 1){
            skateIcon.setInteractive();
        }
        if(this.rollPoss === 1){
            rollIcon.setInteractive();
        }
        if(this.trotPoss === 1){
            trotIcon.setInteractive();
        }
        if(this.veloPoss === 1){
            veloIcon.setInteractive();
        }
        if(this.scootPoss === 1){
            scootIcon.setInteractive();
        }


        // piedIcon interactif
        piedIcon.setInteractive();
        piedIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: piedIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        piedIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: piedIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        piedIcon.on("pointerup", ()=>{
            this.time.addEvent({
                delay: 500,
                callback: ()=>{

                    this.tweens.add({
                        targets: fondNoir,
                        x: 1000,
                        y: 0,
                        ease: 'Sine.easeInOut',
                        duration: 500,
                        yoyo: false,
                        repeat: 0,
                    });

                },
                loop: false

            })
            console.log('pointerover');
            this.scene.start(niveau, {id: 1, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })




        // skateIcon interactif
        
        skateIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: skateIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        skateIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: skateIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        skateIcon.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(niveau, {id: 2, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })



        // rollIcon interactif
        
        rollIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: rollIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        rollIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: rollIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        rollIcon.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(CST.SCENES.PLAY, {id: 3, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })




        // trotIcon interactif
       
        trotIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: trotIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        trotIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: trotIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        trotIcon.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(CST.SCENES.PLAY2, {id: 4, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })




        // veloIcon interactif
        
        veloIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: veloIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        veloIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: veloIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        veloIcon.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(CST.SCENES.PLAY2, {id: 5, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })




        // scootIcon interactif
        
        scootIcon.on("pointerover", ()=>{
            this.tweens.add({
                targets: scootIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                ease: 'easeIn',
                duration: 75,
                yoyo: false,
                repeat: 0
            })
            
        })
        scootIcon.on("pointerout", ()=>{
            this.tweens.add({
                targets: scootIcon,
                scaleX: 0.02,
                scaleY: 0.02,
                ease: 'easeIn',
                duration: 125,
                yoyo: false,
                repeat: 0
            })
        })
        scootIcon.on("pointerup", ()=>{
            console.log('pointerover');
            this.scene.start(CST.SCENES.PLAY2, {id: 6, pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        })





        var info = this.make.text({
            x: 520,
            y: 350,
            text: 'CHOISISSEZ VOTRE MOYEN DE TRANSPORT',
            style: {
                font: '15px Arcon',
                fill: '#ffffff'
            }
        });
        info.setOrigin(0.5, 0.5);
    }
    
    update(){

    }
}
