import { CST } from "../CST";
export class ShopScene extends Phaser.Scene {
    /**
     * 
     */
    constructor(){
        super({
            key: CST.SCENES.SHOP
        });
        
    }

    init(data){
        console.log('init', data);
        this.nbPieces = data.pieces;
        this.skatePoss = data.skate;
        this.trotPoss = data.trotin;
        this.rollPoss = data.roller;
        this.veloPoss = data.velo;
        this.scootPoss = data.scoot;
    }
    
    preload(){}

    create(){

        this.piecesCount = this.make.text({
            x: 950,
            y: 50,
            text: this.nbPieces,
            style: {
                font: '40px Poppins',
                fill: '#ffffff'
            }
        });
        this.piecesCount.setDepth(200);
        

        // variables booléenes des alternatives

        var skatePrix = 15;
        var rollPrix = 30;
        var trotPrix = 50;
        var veloPrix = 100;
        var scootPrix = 500;

        var width = this.game.renderer.width;

        // mise en place de la touche M si l'on veut retourner sur la map

        this.keyboard = this.input.keyboard.addKeys("M");

        // mise en place des images (devant et arrière) du magasin

        var front = this.add.image(500,270,"frontShop").setScale(0.12).setDepth(5);
        var back = this.add.image(500,250,"backShop").setScale(0.13);

        // animations des différents plans

        this.tweens.add({
            targets: front,
            y: 285,
            ease: 'Sine.easeInOut',
            duration: 3000,
            yoyo: true,
            repeat: -1,
        });

        this.time.addEvent({
            delay: 100,
            callback: ()=>{
            this.tweens.add({
                targets: back,
                y: 255,
                ease: 'Sine.easeInOut',
                duration: 3000,
                yoyo: true,
                repeat: -1,
            });
        },
        loop: false
})

    // mise en place des fiches alternatives

    let ficheSkate = this.add.image(265,265,'ficheSkate').setScale(0.15);
    ficheSkate.alpha = 0;
    let ficheRoller = this.add.image(265,265,'ficheRoller').setScale(0.15);
    ficheRoller.alpha = 0;
    let ficheTrot = this.add.image(265,265,'ficheTrot').setScale(0.15);
    ficheTrot.alpha = 0;
    let ficheVelo = this.add.image(265,265,'ficheVelo').setScale(0.15);
    ficheVelo.alpha = 0;
    let ficheScoot = this.add.image(265,265,'ficheScoot').setScale(0.15);
    ficheScoot.alpha = 0;

    // mise en place des icones

        let iconTaille = 70;

        let rond1 = this.add.image(230 - (iconTaille/2), 35, "rond").setOrigin(0).setDepth(10).setScale(2.7);
        let rond2 = this.add.image(35, 197 - (iconTaille/2), "rond").setOrigin(0).setDepth(11).setScale(2.7);
        let rond3 = this.add.image(430 - iconTaille, 197 - (iconTaille/2), "rond").setOrigin(0).setDepth(12).setScale(2.7);
        let rond4 = this.add.image(105, 410 - iconTaille, "rond").setOrigin(0).setDepth(13).setScale(2.7);
        let rond5 = this.add.image(355 - iconTaille, 410 - iconTaille, "rond").setOrigin(0).setDepth(14).setScale(2.7);

        this.srcSkateIcon = "skateIcon";
        this.srcRollIcon = "rollIcon";
        this.srcTrotIcon = "trotIcon";
        this.srcVeloIcon = "veloIcon";
        this.srcScootIcon = "scootIcon";

        if(this.skatePoss === 1){
            this.srcSkateIcon = "skateIconNon";
        }
        if(this.rollPoss === 1){
            this.srcRollIcon = "rollIconNon";
        }
        if(this.trotPoss === 1){
            this.srcTrotIcon = "trotIconNon";
        }
        if(this.veloPoss === 1){
            this.srcVeloIcon = "veloIconNon";
        }
        if(this.scootPoss === 1){
            this.srcScootIcon = "scootIconNon";
        }

        let skateIcon = this.add.image(250 - (iconTaille/2), 50, this.srcSkateIcon).setOrigin(0).setDepth(27).setScale(0.02);
        let rollIcon = this.add.image(55, 212 - (iconTaille/2), this.srcRollIcon).setOrigin(0).setDepth(28).setScale(0.02);
        let trotIcon = this.add.image(450 - iconTaille, 212 - (iconTaille/2), this.srcTrotIcon).setOrigin(0).setDepth(30).setScale(0.02);
        let veloIcon = this.add.image(125, 425 - iconTaille, this.srcVeloIcon).setOrigin(0).setDepth(24).setScale(0.02);
        let scootIcon = this.add.image(375 - iconTaille, 425 - iconTaille, this.srcScootIcon).setOrigin(0).setDepth(25).setScale(0.02);



    // animations des icones

    // skateIcon interactif

    this.prixSkate = this.make.text({
        x: 240,
        y: 0,
        text: skatePrix+' C',
        style: {
            font: '30px Poppins',
            fill: '#ffffff'
        }
    });
    
    skateIcon.setInteractive();
    skateIcon.on("pointerover", ()=>{
        this.tweens.add({
            targets: skateIcon,
            scaleX: 0.025,
            scaleY: 0.025,
            x: 237 - (iconTaille/2),
            y: 42,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheSkate,
            alpha: 1,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        
    })
    skateIcon.on("pointerout", ()=>{
        this.tweens.add({
            targets: skateIcon,
            scaleX: 0.02,
            scaleY: 0.02,
            x:250 - (iconTaille/2),
            y:50,
            ease: 'easeIn',
            duration: 125,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheSkate,
            alpha: 0,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
    })
    skateIcon.on("pointerup", ()=>{
        console.log(this.skatePoss);
        if(this.skatePoss === 0 && this.nbPieces >= skatePrix){
            this.skatePoss = 1;
            this.nbPieces = this.nbPieces - skatePrix;
            console.log(this.nbPieces);
            skateIcon.alpha = 0.5;
        } else {
            this.tweens.add({
                targets: skateIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                x: 230 - (iconTaille/2),
                y:42,
                ease: 'Sine.easeInOut',
                duration: 100,
                yoyo: true,
                repeat: 2,
            });
        }
    })



    // rollIcon interactif

    this.prixRoller = this.make.text({
        x: 85,
        y: 125,
        text: rollPrix+' C',
        style: {
            font: '30px Poppins',
            fill: '#ffffff'
        }
    });
    rollIcon.setInteractive();
    rollIcon.on("pointerover", ()=>{
        this.tweens.add({
            targets: rollIcon,
            scaleX: 0.025,
            scaleY: 0.025,
            x: 42,
            y: 204 - (iconTaille/2),
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheRoller,
            alpha: 1,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        
    })
    rollIcon.on("pointerout", ()=>{
        this.tweens.add({
            targets: rollIcon,
            scaleX: 0.02,
            scaleY: 0.02,
            x:55,
            y:212 - (iconTaille/2),
            ease: 'easeIn',
            duration: 125,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheRoller,
            alpha: 0,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
    })
    rollIcon.on("pointerup", ()=>{
        console.log(this.rollPoss);
        if(this.rollPoss === 0 && this.nbPieces >= rollPrix){
            this.rollPoss = 1;
            this.nbPieces = this.nbPieces - rollPrix;
            console.log(this.nbPieces);
            rollIcon.alpha = 0.5;
        } else {
            this.tweens.add({
                targets: rollIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                x: 27,
                y:204 - (iconTaille/2),
                ease: 'Sine.easeInOut',
                duration: 100,
                yoyo: true,
                repeat: 2,
            });
        }
    })




    // trotIcon interactif
    this.prixTrot = this.make.text({
        x: 405,
        y: 125,
        text: trotPrix+' C',
        style: {
            font: '30px Poppins',
            fill: '#ffffff'
        }
    });
    trotIcon.setInteractive();
    trotIcon.on("pointerover", ()=>{
        this.tweens.add({
            targets: trotIcon,
            scaleX: 0.025,
            scaleY: 0.025,
            x:437 - iconTaille,
            y:204 - (iconTaille/2),
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheTrot,
            alpha: 1,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        
    })
    trotIcon.on("pointerout", ()=>{
        this.tweens.add({
            targets: trotIcon,
            scaleX: 0.02,
            scaleY: 0.02,
            x:450 - iconTaille,
            y:212 - (iconTaille/2),
            ease: 'easeIn',
            duration: 125,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheTrot,
            alpha: 0,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
    })
    trotIcon.on("pointerup", ()=>{
        console.log(this.trotPoss);
        if(this.trotPoss === 0 && this.nbPieces >= trotPrix){
            this.trotPoss = 1;
            this.nbPieces = this.nbPieces - trotPrix;
            console.log(this.nbPieces);
            trotIcon.alpha = 0.5;
        } else {
            this.tweens.add({
                targets: trotIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                x:422 - iconTaille,
                y:204 - (iconTaille/2),
                ease: 'Sine.easeInOut',
                duration: 100,
                yoyo: true,
                repeat: 2,
            });
        }
    })




    // veloIcon interactif
    this.prixVelo = this.make.text({
        x: 135,
        y: 305,
        text: veloPrix+' C',
        style: {
            font: '30px Poppins',
            fill: '#ffffff'
        }
    });
    veloIcon.setInteractive();
    veloIcon.on("pointerover", ()=>{
        this.tweens.add({
            targets: veloIcon,
            scaleX: 0.025,
            scaleY: 0.025,
            x:112,
            y:417 - iconTaille,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheVelo,
            alpha: 1,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        
    })
    veloIcon.on("pointerout", ()=>{
        this.tweens.add({
            targets: veloIcon,
            scaleX: 0.02,
            scaleY: 0.02,
            x:125,
            y:425 - iconTaille,
            ease: 'easeIn',
            duration: 125,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheVelo,
            alpha: 0,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
    })
    veloIcon.on("pointerup", ()=>{
        console.log(this.veloPoss);
        
        if(this.veloPoss === 0 && this.nbPieces >= veloPrix){
            this.veloPoss = 1;
            this.nbPieces = this.nbPieces - veloPrix;
            veloIcon.alpha = 0.5;
        } else {
            this.tweens.add({
                targets: veloIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                x:97,
                y:417 - iconTaille,
                ease: 'Sine.easeInOut',
                duration: 100,
                yoyo: true,
                repeat: 2,
            });
        }
    })





    // scootIcon interactif
    this.prixScooter = this.make.text({
        x: 315,
        y: 305,
        text: scootPrix+' C',
        style: {
            font: '30px Poppins',
            fill: '#ffffff'
        }
    });
    scootIcon.setInteractive();
    scootIcon.on("pointerover", ()=>{
        this.tweens.add({
            targets: scootIcon,
            scaleX: 0.025,
            scaleY: 0.025,
            x:362 - iconTaille,
            y:417 - iconTaille,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheRoller,
            alpha: 1,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
        
    })
    scootIcon.on("pointerout", ()=>{
        this.tweens.add({
            targets: scootIcon,
            scaleX: 0.02,
            scaleY: 0.02,
            x:375 - iconTaille,
            y:425 - iconTaille,
            ease: 'easeIn',
            duration: 125,
            yoyo: false,
            repeat: 0
        })
        this.tweens.add({
            targets: ficheRoller,
            alpha: 0,
            ease: 'easeIn',
            duration: 75,
            yoyo: false,
            repeat: 0
        })
    })
    scootIcon.on("pointerup", ()=>{
        console.log(this.scootPoss);
        if(this.scootPoss === 0 && this.nbPieces >= scootPrix){
            this.scootPoss = 1;
            this.nbPieces = this.nbPieces - scootPrix;
            console.log(this.nbPieces);
            scootIcon.alpha = 0.5;
        } else {
            this.tweens.add({
                targets: scootIcon,
                scaleX: 0.025,
                scaleY: 0.025,
                x:347 - iconTaille,
                y:417 - iconTaille,
                ease: 'Sine.easeInOut',
                duration: 100,
                yoyo: true,
                repeat: 2,
            });
        }
    })


    var Mbutton = this.make.text({
        x: width,
        y: 480,
        text: 'APPUYER SUR M POUR RETOURNER AU MENU',
        style: {
            font: '20px Poppins',
            fill: '#ffffff'
        }
    });
    Mbutton.setOrigin(1.1, 0.5).setDepth(80);

    }

    update(){
        if(this.keyboard.M.isDown === true){
            this.scene.start(CST.SCENES.MAP, {pieces: this.nbPieces, skate: this.skatePoss, roller: this.rollPoss, trotin: this.trotPoss, velo: this.veloPoss, scoot: this.scootPoss});
        }
        this.piecesCount.setText(this.nbPieces);

        if(this.skatePoss === 1){
            this.prixSkate.setText('VENDU');
            this.srcSkateIcon = "skateIconNon";
        }
        if(this.rollPoss === 1){
            this.prixRoller.setText('VENDU');
            this.srcRollIcon = "rollIconNon";
        }
        if(this.trotPoss === 1){
            this.prixTrot.setText('VENDU');
            this.srcTrotIcon = "trotIconNon";
        }
        if(this.veloPoss === 1){
            this.prixVelo.setText('VENDU');
            this.srcSkateIcon = "veloIconNon";
        }
        if(this.scootPoss === 1){
            this.prixScooter.setText('VENDU');
            this.srcScootIcon = "scootIconNon";
        }
    }
}
